import React, { useState, useEffect } from "react";
import Title from "./title";

const OptionsButton = ({
  section,
  reward,
  optionNum,
  skill,
  pass,
  action,
  text,
}) => {
  if (section.includes("quest")) {
    return (
      <div class="col-2">
        <div class="card">
          <div class="card-body">
            <h5 class="card-title">
              <u>Option{optionNum + 1}</u>{" "}
            </h5>
            <p class="card-text">
              <br></br>
              {text}
              <br></br>
              {skill}
              <br></br>
              {/* {reward.success ? (
                <p>
                  <u>Success </u>: {reward.success}
                </p>
              ) : (
                <>{reward}</>
              )} */}
              <>{reward}</>
            </p>

            <button class="btn btn-primary">{action}</button>
          </div>
        </div>
      </div>
    );
  }

  if (section.includes("travel")) {
    return (
      <div>
        <br />
        <p>
          <b>
            <u>Option</u> {optionNum + 1}
          </b>
        </p>
        <p> {text}</p>
        <br></br>
        <button> {action}</button>
        <br />
        ___________________________________________________
      </div>
    );
  }
  if (section.includes("event")) {
    return (
      <div class="col-2">
        <div class="card">
          <div class="card-body">
            <h5 class="card-title">
              <u>Option{optionNum + 1}</u>{" "}
            </h5>
            <p class="card-text">
              {text}
              <br></br>
              <u>{skill}</u>
            </p>

            <button class="btn btn-primary">{action}</button>
          </div>
        </div>
      </div>
    );
  }
  if (section.includes("people")) {
    return (
      <div class="col-2">
        <div class="card">
          <div class="card-body">
            <h5 class="card-title">
              <u>Option{optionNum + 1}</u>{" "}
            </h5>
            <p class="card-text">
              {text}
              <br></br>
              <u>{skill}</u>
            </p>

            <button class="btn btn-primary">{action}</button>
          </div>
        </div>
      </div>
    );
  }
};

export default OptionsButton;
